import React, {} from 'react'
import Card from './Card'
import list from './data'
import '../styles/amazon.css'
const Amazon = ({handelClick}) => {   
    
  return (
    <section>
        {
            list.map((item) => (
            <Card key={item.id} item={item} handelClick={handelClick}/>
            ))
        }
    </section>
  )
}

export default Amazon