import React from 'react'
import '../styles/card.css'

const Card = ({item, handelClick}) => {
    //console.log(item)
    const {title, author , price, img} = item;
  return (
    <div className='cards'>
        <div className='image-box'>
            <img src={img} alt="" />
        </div>
        <div className='details'>
            <p>{title}</p>
            <p>{author}</p>
            <p>{price}</p>
            <button onClick= {()=> handelClick(item)}>Add to Cart</button>
        </div>
    </div>
  )
}

export default Card